*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html,
body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  background-color: #333232; }

h1,
h2,
h3 {
  margin: 0;
  padding: 0; }

.container {
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  width: 100%;
  max-width: 1100px;
  margin-left: auto;
  margin-right: auto; }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }

li {
  padding: 0;
  margin: 0; }

#header {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  text-align: center;
  color: #e4e4e4;
  background-color: #006ff6;
  text-transform: uppercase; }
  #header .title {
    letter-spacing: 1px;
    margin-bottom: 0.5rem;
    font-size: 1rem; }
  #header .clue {
    font-size: 1rem;
    margin-bottom: 0.5rem; }
  #header .color-number {
    font-weight: 300;
    letter-spacing: 2px;
    font-size: 2rem; }
  @media (orientation: landscape) {
    #header {
      padding-top: 0.5rem;
      padding-bottom: 0.5rem; }
      #header .title,
      #header .clue,
      #header .color-number {
        font-size: 1rem;
        margin-bottom: .5rem; } }
  @media (orientation: landscape) and (min-width: 900px) {
    #header {
      padding-top: 0.5rem;
      padding-bottom: 0.5rem; }
      #header .title {
        letter-spacing: 1px;
        margin-bottom: 1rem;
        font-size: 1rem; }
      #header .clue {
        font-size: 0.8rem; }
      #header .color-number {
        font-size: 1.4rem; } }

#navigation {
  background-color: #e4e4e4;
  font-size: 1rem;
  text-transform: uppercase;
  color: #006ff6;
  font-weight: 700; }
  #navigation .list-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0.5rem;
    font-size: 1rem; }
    #navigation .list-container .left-list,
    #navigation .list-container .right-list {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center; }
      #navigation .list-container .left-list li,
      #navigation .list-container .right-list li {
        padding: 0.4rem;
        min-width: 100px;
        text-align: center;
        transition: all .2s linear;
        cursor: pointer; }
        #navigation .list-container .left-list li.play-again:hover, #navigation .list-container .left-list li.option:hover,
        #navigation .list-container .right-list li.play-again:hover,
        #navigation .list-container .right-list li.option:hover {
          background-color: #006ff6;
          color: #e4e4e4; }
    @media (min-width: 650px) {
      #navigation .list-container {
        font-size: 1.2rem; }
        #navigation .list-container .left-list,
        #navigation .list-container .right-list {
          flex-direction: row; }
        #navigation .list-container .right-list {
          justify-content: flex-end; }
          #navigation .list-container .right-list li {
            padding-left: 1rem;
            padding-right: 1rem; } }

#board {
  width: 100%;
  max-width: 360px;
  padding: 0.5rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem; }
  #board .box {
    width: 100px;
    height: 100px;
    border-radius: 10px;
    cursor: pointer; }
  @media (min-width: 650px) {
    #board {
      max-width: 650px; }
      #board .box {
        width: 200px;
        height: 200px; } }

.active {
  background-color: #006ff6;
  color: #e4e4e4; }
